import { Link } from 'react-router-dom';
import { IBook } from '../../shared/types';
import './index.css';

const Book = (props: IBook) => {
	const { title, authors, description, rating, image_url, id } = props;
	const shortDescription = `${description?.substring(0, 300)}...`;

	return (
		<Link to={`books/${id}`}>
			<div className="bookWrapper">
				<div className="bookHeader">
					<div className="cover">
						<img src={image_url} alt="Cover" />
					</div>
					<div className="info">
						<h3 className="title">{title}</h3>
						<p className="authors">{authors}</p>
						<p>Rating: {rating}</p>
					</div>
				</div>
				<p className="description">{shortDescription}</p>
			</div>
		</Link>
	);
};

export default Book;
